import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { ref, onValue, remove } from 'firebase/database';
import { database } from '../../firebase';

const AdminPosts = () => {
  const [posts, setPosts] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');

  useEffect(() => {
    const postsRef = ref(database, 'posts');

    const unsubscribe = onValue(postsRef, (snapshot) => {
      setLoading(true);

      try {
        const postsData = snapshot.val() || {};

        // Chuyển đổi đối tượng posts thành mảng và sắp xếp theo thời gian tạo (mới nhất trước)
        const postsArray = Object.keys(postsData).map(id => ({
          id,
          ...postsData[id]
        })).sort((a, b) => b.createdAt - a.createdAt);

        setPosts(postsArray);
        setError('');
      } catch (error) {
        console.error('Lỗi khi tải dữ liệu bài viết:', error);
        setError('Không thể tải dữ liệu bài viết. Vui lòng thử lại sau.');
      } finally {
        setLoading(false);
      }
    });

    // Cleanup function
    return () => unsubscribe();
  }, []);

  const handleDeletePost = async (postId) => {
    if (window.confirm('Bạn có chắc chắn muốn xóa bài viết này?')) {
      try {
        // Xóa bài viết
        await remove(ref(database, `posts/${postId}`));

        // Xóa bình luận liên quan
        await remove(ref(database, `comments/${postId}`));

        // Xóa đánh giá liên quan
        await remove(ref(database, `ratings/${postId}`));

        // Không cần cập nhật state vì onValue listener sẽ tự động cập nhật

      } catch (error) {
        console.error('Lỗi khi xóa bài viết:', error);
        setError('Không thể xóa bài viết. Vui lòng thử lại sau.');
      }
    }
  };

  const formatDate = (timestamp) => {
    if (!timestamp) return '';
    const date = new Date(timestamp);
    return date.toLocaleDateString('vi-VN', {
      year: 'numeric',
      month: 'short',
      day: 'numeric'
    });
  };

  return (
    <div className="admin-posts-container">
      <div className="header-actions">
        <h1>Quản lý bài viết</h1>
        <Link to="/admin/posts/create" className="btn btn-success">Tạo bài viết mới</Link>
      </div>

      {error && <div className="error-message">{error}</div>}

      {loading ? (
        <div className="loading">
          <div className="spinner"></div>
        </div>
      ) : posts.length > 0 ? (
        <div className="admin-posts-table">
          <table>
            <thead>
              <tr>
                <th>Tiêu đề</th>
                <th>Danh mục</th>
                <th>Ngày đăng</th>
                <th>Lượt xem</th>
                <th>Đánh giá</th>
                <th>Thao tác</th>
              </tr>
            </thead>
            <tbody>
              {posts.map(post => (
                <tr key={post.id}>
                  <td><Link to={`/post/${post.id}`}>{post.title}</Link></td>
                  <td>{post.category}</td>
                  <td>{formatDate(post.createdAt)}</td>
                  <td>{post.viewCount || 0}</td>
                  <td>
                    {post.averageRating ? (
                      <span>⭐ {post.averageRating.toFixed(1)} ({post.ratingCount})</span>
                    ) : (
                      <span>Chưa có đánh giá</span>
                    )}
                  </td>
                  <td className="actions">
                    <Link to={`/admin/posts/edit/${post.id}`} className="btn-edit">Sửa</Link>
                    <button
                      onClick={() => handleDeletePost(post.id)}
                      className="btn-delete"
                    >
                      Xóa
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      ) : (
        <p className="no-posts">Chưa có bài viết nào.</p>
      )}
    </div>
  );
};

export default AdminPosts;
